.typing-text-line {
  white-space: nowrap;
}

.typing-text-word {
  visibility: hidden;
}

.cursor {
  display: inline-block;
  width: 1ch;
  animation: blink 0.7s steps(2) infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
